
    .about{
        margin: 3% 0 0 0;
    }
    
    .title{
        width: 100%;
        font-size: 2em;
        font-weight: 900;
    }
    
    .aboutDescription{
        margin: 1% 0 0 0;
        text-indent: 20px;
        font-weight: bolder;
    }

    a{
        text-decoration: none;
    }

    #gamesDescription, #programsDescription{
        display: none;
    }

    #gamesBtn, #programsBtn{
        width: 100%;
        height: 50%;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
        align-items: center;
    }

    button {
        margin: 3% 0 0 0;
        width: 80px;
        height: 80px;
        background-color: rgb(50, 100, 168);
        border: 1px solid rgb(50, 100, 168);
        border-radius: 15px;
        outline: none;
        color: #fff;
        font-weight: bolder;
    }

    button:active{
        background-color: cornflowerblue;
        box-shadow: inset 0 0 15px #000;
        color: #000;
    }

    @media screen and (min-width: 768px) {

        #games, #programs{
            display:flex;
            flex-wrap: wrap;
            justify-content: space-around;
            align-items: center;
        }        
     #gamesBtn, #programsBtn{
         width: 49%;
    }
     #gamesDescription, #programsDescription{
        width: 49%;
        display: block;
        font-weight: bolder;
        text-align: center;
     }
    }

  